import { Directive } from '@angular/core';
import { StyleBuilder } from '../classes/style-builder';
import { StyleDeclaration } from '../interfaces/style-declaration';

@Directive({
  selector: '[dgElevation]',

  inputs: [
    'input: dgElevation'
  ]
})
export class ElevationDirective extends StyleBuilder {
  public build(input: string): StyleDeclaration {
    return {
      'box-shadow': `var(--elevation-z${input}--box-shadow)`
    };
  }
}
