import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class TypeService {
  public isVoid(value: any): value is void {
    return value === undefined || value === null;
  }

  public toString(value: any): string | null {
    if (!this.isVoid(value)) {
      return String(value);
    }

    return null;
  }

  public toNumber(value: any): number | null {
    if (!this.isVoid(value)) {
      return Number(value);
    }

    return null;
  }

  public toBoolean(value: any): boolean | null {
    if (!this.isVoid(value)) {
      if (typeof value === 'string') {
        return value !== 'false';
      }

      return Boolean(value);
    }

    return null;
  }

  public toDate(value: any): Date | null {
    if (!this.isVoid(value)) {
      let date = new Date(value);

      return new Date(
        Date.UTC(
          date.getFullYear(),
          date.getMonth(),
          date.getDate(),
          date.getHours(),
          date.getMinutes(),
          date.getSeconds(),
          date.getMilliseconds()
        )
      );
    }

    return null;
  }
}
