import { ElementRef, KeyValueChanges, KeyValueDiffer, KeyValueDiffers, Renderer2, RendererStyleFlags2 } from '@angular/core';
import { StyleDeclaration } from '../interfaces/style-declaration';
import { TypeService } from '../services/type.service';

export abstract class StyleBuilder {
  public set input(value: any) {
    let input = this._type.toString(value);

    {
      let changes = this._differ.diff(input ? this.build(input) : {});

      if (changes) {
        this._applyChanges(changes);
      }
    }
  }

  private _differ: KeyValueDiffer<string, string | null> = this._keyValueDiffers.find({}).create();

  public constructor(
    private _elementRef: ElementRef<HTMLElement>,
    private _keyValueDiffers: KeyValueDiffers,
    private _renderer: Renderer2,

    private _type: TypeService
  ) {}

  protected abstract build(input: string): StyleDeclaration;

  private _applyChanges(changes: KeyValueChanges<string, string | null>): void {
    changes.forEachItem(({ key: style, currentValue: value }) => {
      this._renderer.setStyle(this._elementRef.nativeElement, style, value, RendererStyleFlags2.DashCase);
    });
  }
}
